<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" :showCheckBox="true" :listType="[DetailListType]" ModuleName="Timer"
            SubModuleCode="Timer" IdentityColumn="category_id" HeaderText="Category" :ListData="CategorytList"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy"  :IsFilterApplied="searchCondition?true:false"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                
                <template v-if="data.column.COLUMN_NAME == 'status_name'">
                    <a class="inactive-disabled">
                        <span class="btn d-inline-block text-center text-truncate btn-success">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'category_name'">
                    <span class="current-color-list" :style="{ backgroundColor: data.row.color_code }"></span>
                    {{ data.row.category_name }}
                </template>
                <template v-if="data.column.COLUMN_NAME == 'applicationWithIcon'" >
                    <div class="d-flex iconsWrapper align-items-center" title="">
                    <div class="mr-2" v-for="(item, itemIndex) in data.row.applicationFirstThree" :key="itemIndex">
                        <span :class="item.icon_class ? 'appicon ' + item.icon_class : 'appicon default'" :title="item.application_display_name">
                      
                        </span>
                        <em href="#" :title="item.application_display_name">
                                {{ TruncateStr(item.application_display_name, 20) }}
                        </em>
                    </div>
                        <span  v-if="data.row.isVisible == true" >
                             <em  aria-hidden="true" data-toggle="modal"   :data-target="'#myModal' + data.row.ROWNUMBER"
                                class="fa fa-user-o text-dark" ></em>
                        </span>
                        <span v-else>
                        </span>
                </div>    

                        <div class="application_dtls position-relative">
                            <div class="modal" :id="'myModal' + data.row.ROWNUMBER">
                                <div class="modal-dialog " role="document" style="max-width: auto;" >
                                    <div class="modal-content ">

                                        <!-- Modal Header -->
                                        <div class="modal-header p-2 bg-grey">
                                            <h6 class="modal-title">Apps/Websites</h6>

                                        </div>

                                        <!-- Modal body -->
                                        <div class="modal-body px-2 py-0" style=" max-height: 300px; overflow-y: auto;">
                                            <table class="w-100">                                            
                                                <tbody>
                                                    <tr v-for="(item, itemIndex) in data.row.applicationWithIcon" class="d-flex iconsWrapper align-items-center" :key="itemIndex">
                                                        <td :class="item.icon_class ? 'appicon ' + item.icon_class : 'appicon default'" :title="item.application_display_name"></td>
                                                        <td :title="item.application_display_name" class="">{{ item.application_display_name }}</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>

                                        <!-- Modal footer -->
                                        <!-- <div class="modal-footer p-2">
                                        <button type="button" class="btn py-1 btn-danger" data-dismiss="modal">Close</button>
                                    </div> -->

                                    </div>
                                </div>
                            </div>
                        </div>
                    </template>
            </template>
        </tg-list>
        <ManageCategory v-if="IsAssignCategory" :CategorytList="CategorytList" :AssignCat='AssignCat'></ManageCategory>
    </section>
</template>

<script>
import Schema from '../TimeTracking/CategoryListingSchema'
import DataService from '../../services/DataService';
import ImportCategory from '../TimeTracking/ImportCategory.vue';
import ManageCategory from '../../components/Modal/ManageCategory.vue';
export default {
    components: {
        ManageCategory,
        ImportCategory
    },

    data() {
        return {
            searchCondition:'',
            CategorytList: [],
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            widgets: [],
            Headers: [

                {
                    "COLUMN_NAME": 'category_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Category'),
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true,
                    },

                },
                {
                    "COLUMN_NAME": 'applicationWithIcon',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ApplicationWebsite'),
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": 'user_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Createdby'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'last_updated',
                    "DATA_TYPE": "datetime",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Lastupdated'),
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": 'status_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true,
                    },
                }

            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: '',
            sortExp: '',
            pageSize: 10,
            pageNum: 1,
            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }

            ],
            LegendArray: [],
            PageSizeOption: null,
            listheaderbuttons: Schema.buttons,
            isLoading: false,
            DetailListType: 'List',
            IsAssignCategory: false,
            usericon:false,
            user_ids:'',
            category_ids:'',
            department_ids:'',
            project_ids:''
        }
    },
    created() {
        debugger
        this.FetchData();
   
        Schema.bindleftCommonSearchdropdown(this, "Timer", "USERS");
        Schema.bindleftCommonSearchdropdown(this, "Timer", "Category");
        Schema.bindleftCommonSearchdropdown(this, "Timer", "DEPARTMENT");
        Schema.bindleftCommonSearchdropdown(this, "Timer", "PROJECT");

    },
    methods: {
        AssignCategoryClick() {
            var vm = this;
            vm.IsAssignCategory = true;
        },
        CloseCategoryClick() {
            var vm = this;
            vm.AssignCat = [];
            vm.IsAssignCategory = false;
        },
        EditAssignCategoryClick: function (value) {
            var vm = this;
            vm.AssignCat = value;
            vm.IsAssignCategory = true;
        },
        async FetchData() {
       
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `${vueObj.searchCondition}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&user_id=${vueObj.user_ids}&category_id=${vueObj.category_ids}&department_id=${vueObj.department_ids}&project_id=${vueObj.project_ids}`;
            await DataService.GetCategoryListing(url).then(response => {
             
                vueObj.CategorytList = JSON.parse(response.data).data;
                // console.log("Website", vueObj.CategorytList)
                vueObj.isLoading = false;
                if(vueObj.CategorytList.length>0)
                {
                vueObj.TotalRecords = vueObj.CategorytList[0].TotalRecords;
                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                vueObj.CurrentPage = vueObj.pageNum;
                vueObj.noRecord = false;
                vueObj.isLoading = false;
                
              vueObj.CategorytList.forEach(i=>{
               i.isVisible=isVisiblee;
             })
                vueObj.CategorytList.forEach(items=>{
                    if(items.applicationWithIcon==null)
                    {
                        /// null conditionb
                       items.isVisible=false;
                    }
                    else{
                       if(items.applicationWithIcon.length>3){
                        items.isVisible=true;
                       }
                       else{
                        items.isVisible=false;
                       }
                    }
                        
                })
                }
                else
                {
                    vueObj.CategorytList=[]
                    vueObj.noRecord = true;
                    vueObj.isLoading = false;
                }
                
            })
            let isVisiblee=false;
            setTimeout(function () {
                vueObj.CheckBoxBootstrap();
            }, 100);
        },
        DeleteData: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("category_id")), function () {
                    let url = `ids=${val}&tableName=TALYGEN_category&primaryKey=category_id`;
                    DataService.Delete(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
        },
        SingleDeleteData: function (item) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("category_id")), function () {
                let url = `ids=${item.category_id}&tableName=TALYGEN_category&primaryKey=category_id`;
                DataService.Delete(url).then(response => {
                    if (response.data == 'DeletedSuccess') {
                        vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        vm.$router.push({
                            name: 'CategoryListing'
                        });
                        vm.FetchData();
                    } else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                    }
                });
            });
        },
        actionButtonClick(callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'Editdata':
                    this.EditAssignCategoryClick(item)
                    break;
                case 'DeleteData':
                    vm.SingleDeleteData(item)
                    break;
            }
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "AssignCategoryClick":
                    this.AssignCategoryClick();
                    break;
                case "Editdata":
                    this.Editdata()
                    break;
                case "DeleteData":
                    this.DeleteData()
                    break;
                case "ViewImport":
                    this.ViewImport()
                    break;

            }
        },

        leftCommonSearch: function (value) {
            debugger
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        ViewImport: function () {
            this.$router.push({
                name: 'ImportCategory'
            });
        },
    }
}
</script>

<style>
.current-color-list {
    display: inline-block;
    width: 17px !important;
    height: 9px !important;
    background-color: #000;
    cursor: pointer;
}

.application_dtls .modal-dialog {
    max-width: 250px;
    position: absolute;
    position: absolute;
    left: 33%;
    top: 100px;
}
.iconsWrapper .appicon {
    transform: scale(0.7);
    margin-right: 0;
}

.iconsWrapper a {
    color: #333;
    font-size: 10px;
}
</style>
